package fry.lab.app.app.mainScreen

import androidx.compose.foundation.background
import androidx.compose.foundation.border
import androidx.compose.foundation.gestures.ScrollableDefaults
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material3.Surface
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.dp
import fry.lab.app.app.widgets.DropDownButtons
import fry.lab.app.app.widgets.LanguageButtons
import fry.lab.app.app.widgets.MainInputs
import fry.lab.app.app.widgets.StyleBlueButtons

@Composable
fun FirstScreen(
    component: FirstScreenComponent
) {
    val flingBehavior = ScrollableDefaults.flingBehavior()

    Surface(
        modifier = Modifier
            .fillMaxSize()
            .background(color = Color(0xFFF6F6F6))
    ) {
        LazyColumn(
            horizontalAlignment = Alignment.CenterHorizontally,
            flingBehavior = flingBehavior
        ) {
            item {
                Spacer(Modifier.height(16.dp))
                LanguageButtons()
            }

            item {
                Column(
                    modifier = Modifier
                        .padding(32.dp)
                        .border(width = 1.dp, color = Color.Gray, shape = RoundedCornerShape(8.dp))
                        .background(color = Color(0xFFFFFFFF))
                        .padding(32.dp)
                ) {
                    DropDownButtons()
                    MainInputs()
                    StyleBlueButtons(
                        onClick = { title -> component.onNavigateToSecondScreen(title = title) }
                    )
                }
            }
        }
    }
}