package fry.lab.app.app.root

import com.arkivanov.decompose.ComponentContext
import com.arkivanov.decompose.router.stack.ChildStack
import com.arkivanov.decompose.router.stack.StackNavigation
import com.arkivanov.decompose.router.stack.childStack
import com.arkivanov.decompose.router.stack.pop
import com.arkivanov.decompose.router.stack.popTo
import com.arkivanov.decompose.router.stack.push
import com.arkivanov.decompose.value.Value
import fry.lab.app.app.mainScreen.DefaultFirstScreenComponent
import fry.lab.app.app.mainScreen.FirstScreenComponent
import fry.lab.app.app.settingsScreen.DefaultSecondScreenComponent
import fry.lab.app.app.settingsScreen.SecondScreenComponent
import fry.lab.app.data.ArtistParams
import fry.lab.app.data.BlueStyles
import fry.lab.app.data.CameraParams
import fry.lab.app.data.ColorParams
import fry.lab.app.data.LightingParams
import fry.lab.app.data.MaterialsParams
import fry.lab.app.data.StylesParams
import kotlinx.collections.immutable.toImmutableList

internal class DefaultRootComponent(
    componentContext: ComponentContext
) : RootComponent, ComponentContext by componentContext {

    private val navigation = StackNavigation<Config>()

    override val stack: Value<ChildStack<*, RootComponent.Child>> =
        childStack(
            source = navigation,
            serializer = null,
            initialConfiguration = Config.Screen1,
            handleBackButton = true,
            childFactory = ::child,
        )

    override fun onBackClicked(toIndex: Int) {
        navigation.popTo(index = toIndex)
    }

    private fun child(
        config: Config,
        childComponentContext: ComponentContext
    ): RootComponent.Child =
        when (config) {
            is Config.Screen1 -> RootComponent.Child.Screen1(screen1Component(childComponentContext))
            is Config.Screen2 -> RootComponent.Child.Screen2(
                screen2Component(componentContext = childComponentContext, title = config.title)
            )
        }

    private fun screen1Component(componentContext: ComponentContext): FirstScreenComponent =
        DefaultFirstScreenComponent(
            componentContext = componentContext,
            onShowSecondScreenClicked = { title ->
                navigation.push(Config.Screen2(title = title))
            }
        )

    private fun screen2Component(
        componentContext: ComponentContext,
        title: String
    ): SecondScreenComponent =
        DefaultSecondScreenComponent(
            componentContext = componentContext,
            onBackToFirstScreenClicked = navigation::pop,
            title = title,
            items = when (title) {
                BlueStyles.STYLES.label -> StylesParams.entries
                BlueStyles.LIGHTING.label -> LightingParams.entries
                BlueStyles.CAMERA.label -> CameraParams.entries
                BlueStyles.ARTISTS.label -> ArtistParams.entries
                BlueStyles.COLORS.label -> ColorParams.entries
                BlueStyles.MATERIALS.label -> MaterialsParams.entries
                else -> StylesParams.entries
            }.toImmutableList()
        )

    private sealed interface Config {
        data object Screen1 : Config
        data class Screen2(val title: String) : Config
    }
}