package fry.lab.app.app.mainScreen

import com.arkivanov.decompose.ComponentContext

interface  FirstScreenComponent {
    fun onNavigateToSecondScreen (title: String)
}

class  DefaultFirstScreenComponent (
    componentContext: ComponentContext,
    private  val onShowSecondScreenClicked: (title: String) -> Unit
): FirstScreenComponent, ComponentContext by componentContext {
    override  fun  onNavigateToSecondScreen (title: String) {
        onShowSecondScreenClicked(title)
    }
}