package fry.lab.app.app.widgets

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.ExperimentalLayoutApi
import androidx.compose.foundation.layout.FlowRow
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.width
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import fry.lab.app.data.BlueStyles
import fry.lab.app.views.BlueButton
import kotlinx.collections.immutable.ImmutableList
import kotlinx.collections.immutable.toImmutableList

@Composable
fun StyleBlueButtons(
    modifier: Modifier = Modifier,
    onClick: (title: String) -> Unit
) {

    val firstRowButtons = BlueStyles.entries.toTypedArray().take(2).toImmutableList()
    val secondRowButtons = BlueStyles.entries.drop(2).take(3).toImmutableList()
    val remainingButtons = BlueStyles.entries.drop(5).toImmutableList()

    Column(modifier = modifier.width(400.dp)) {
        BlueRow(buttonList = firstRowButtons, onClick = onClick)
        BlueRow(buttonList = secondRowButtons, onClick = onClick)
        BlueRow(buttonList = remainingButtons, onClick = onClick)
    }
}


@OptIn(ExperimentalLayoutApi::class)
@Composable
private fun BlueRow(
    buttonList: ImmutableList<BlueStyles>,
    onClick: (title: String) -> Unit
) {
    FlowRow(
        modifier = Modifier.fillMaxWidth(),
        horizontalArrangement = Arrangement.Center
    ) {
        buttonList.forEach { buttonItem ->
            BlueButton(
                text = buttonItem.label,
                onClick = { onClick(buttonItem.label) }
            )
        }
    }
}
