package fry.lab.app.app.root

import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material3.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.arkivanov.decompose.extensions.compose.stack.Children
import com.arkivanov.decompose.extensions.compose.stack.animation.fade
import com.arkivanov.decompose.extensions.compose.stack.animation.plus
import com.arkivanov.decompose.extensions.compose.stack.animation.scale
import com.arkivanov.decompose.extensions.compose.stack.animation.stackAnimation
import fry.lab.app.app.mainScreen.FirstScreen
import fry.lab.app.app.settingsScreen.SecondScreen

@Composable
internal fun RootContent(
    component: RootComponent
) {
    MaterialTheme {
        Children(
            stack = component.stack,
            modifier = Modifier.fillMaxSize(),
            animation = stackAnimation(fade() + scale())
        ) {
            when (val instance = it.instance) {
                is RootComponent.Child.Screen1 -> FirstScreen(component = instance.component)
                is RootComponent.Child.Screen2 -> SecondScreen(component = instance.component)
            }
        }
    }
}