@Composable
fun SecondScreen(component: SecondScreenComponent) {
    Box(
        modifier = Modifier
            .fillMaxSize()
            .background(Color.Black.copy(alpha = 0.5f)),
        contentAlignment = Alignment.Center
    ) {
        Box(
            modifier = Modifier
                .width(500.dp)
                .fillMaxHeight()
                .padding(32.dp)
                .clip(RoundedCornerShape(24.dp)),
        ) {
            Content(items = component.getItems())
            TopBar(
                modifier = Modifier.align(Alignment.TopCenter),
                title = component.getTitle(),
                onClickExit = { component.onBackToFirstScreen() }
            )
            BottomBar(modifier = Modifier.align(Alignment.BottomCenter))
        }
    }
}

@Composable
private fun TopBar(
    modifier: Modifier = Modifier,
    title: String,
    onClickExit: () -> Unit
) {
    Box(
        modifier = modifier
            .fillMaxWidth()
            .height(80.dp)
            .background(Color.White)
    ) {
        Text(
            modifier = Modifier.align(Alignment.CenterStart).padding(start = 32.dp),
            text = title,
            style = MaterialTheme.typography.titleLarge.copy(
                fontWeight = FontWeight.Bold
            )
        )
        Box(
            modifier = Modifier
                .fillMaxHeight()
                .align(Alignment.CenterEnd)
                .clickable { onClickExit() }
        ) {
            Icon(
                modifier = Modifier
                    .padding(end = 32.dp, start = 32.dp)
                    .size(24.dp)
                    .align(Alignment.Center),
                painter = painterResource(Res.drawable.ic_cross),
                contentDescription = null
            )
        }
        Box(
            modifier = Modifier
                .fillMaxWidth()
                .height(0.5.dp)
                .background(Color.Gray)
                .align(Alignment.BottomCenter)
        )
    }
}

@Composable
private fun BottomBar(modifier: Modifier = Modifier) {
    Box(
        modifier = modifier
            .fillMaxWidth()
            .height(80.dp)
            .background(Color.White)
    ) {
        Box(
            modifier = Modifier
                .fillMaxWidth()
                .height(0.5.dp)
                .background(Color.Gray)
                .align(Alignment.TopCenter)
        )

        BigBlueButton(
            modifier = Modifier.align(Alignment.CenterStart).padding(start = 16.dp),
            text = "Button",
            onClick = { }
        )
    }
}

@Composable
private fun Content(
    modifier: Modifier = Modifier,
    items: ImmutableList<StyleParamsDto>,
) {
    val flingBehavior = ScrollableDefaults.flingBehavior()
    LazyColumn(
        modifier = modifier
            .fillMaxWidth()
            .background(Color(0xFFF6F6F6)),
        flingBehavior = flingBehavior,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        item { Spacer(Modifier.height(100.dp)) }
        items(items.size) { index ->
            when (val dto = items[index]) {
                is StylesParams -> StyleCard(cardText = dto.title, imgRes = dto.imgRes)
                is LightingParams -> StyleCard(cardText = dto.title, imgRes = dto.imgRes)
                is CameraParams -> StyleCard(cardText = dto.title, imgRes = dto.imgRes)
                is ArtistParams -> StyleCard(cardText = dto.title, imgRes = dto.imgRes)
                is ColorParams -> StyleCard(cardText = dto.title, imgRes = dto.imgRes)
                is MaterialsParams -> StyleCard(cardText = dto.title, imgRes = dto.imgRes)
            }
        }
        item { Spacer(Modifier.height(100.dp)) }
    }
}