package fry.lab.app.app.settingsScreen

import com.arkivanov.decompose.ComponentContext
import fry.lab.app.data.StyleParamsDto
import kotlinx.collections.immutable.ImmutableList

interface SecondScreenComponent {
    fun onBackToFirstScreen()
    fun getTitle(): String
    fun getItems(): ImmutableList<StyleParamsDto>
}

class DefaultSecondScreenComponent(
    componentContext: ComponentContext,
    private val onBackToFirstScreenClicked: () -> Unit,
    private val title: String,
    private val items: ImmutableList<StyleParamsDto>,
) : SecondScreenComponent, ComponentContext by componentContext {

    override fun onBackToFirstScreen() = onBackToFirstScreenClicked()
    override fun getTitle(): String = title
    override fun getItems(): ImmutableList<StyleParamsDto> = items
}